<template>
    <div>
        <router-link to="/add">去添加</router-link>
        <table border="1">
            <thead>
            <tr>
                <th>ID</th>
                <th>姓名</th>
                <th>年龄</th>
                <th>生日</th>
                <th>操作</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="u in users" :key="u.id">
                <td>{{u.id}}</td>
                <td>{{u.name}}</td>
                <td>{{u.age}}</td>
                <td>{{u.bir}}</td>
                <td>
                    <button type="button" @click="del(u.id)">删除</button>
                    <button type="button" @click="update(u.id)">更新</button>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    import {all,del} from "@/api/user";

    export default {
        data(){
            return{
                users:[]
            }
        },
        created(){
            this.getData()
        },
        methods: {
            getData(){
                all().then( resp => {
                    const {status,data,msg}=resp.data
                    if(status){
                        this.users =data
                    }else{
                        alert(msg)
                    }
                    /*if(resp.data.status){
                        this.users = resp.data.data
                    }else{
                        alert(resp.data.msg)
                    }*/
                })
            },
            del(id){
                del(id).then(  resp => {
                    const {status,msg}=resp.data
                    if(status){
                        this.getData()
                    }else{
                        alert(msg)
                    }
                    /*if(resp.data.status){
                        this.getData()
                    }else{
                        alert(resp.data.msg)
                    }*/
                })
            },
            update(id){
                this.$router.push(`/update?id=${id}`)
            }
        }
    }
</script>

