import instance from "@/config/axiosConfig.js"

export function all(){
    return instance.get(`/user/all`)
}

export function one(id){
    return instance.get(`/user/one?id=${id}`)
}
export function add(name, age ,bir){
    //这里的例子是使用JSON方式传递数据，如果采用表单形式，第二个参数为`name=${name}&age=${age}`
    return instance.post("/user/add", {name, age,bir})
}

export function del(id){
    return instance.get(`/user/del?id=${id}`)
}
export function edit(id, name, age ,bir){
    //这里的例子是使用JSON方式传递数据，如果采用表单形式，第二个参数为`name=${name}&age=${age}`
    return instance.post("/user/edit", {id,name, age,bir})
}