import Vue from "vue"
import VueRouter from "vue-router";
import Home from "@/views/Home";
import Add from "@/views/Add";
import Update from "@/views/Update";
Vue.use(VueRouter)//启用VueRouter插件

export default new VueRouter({
    routes:[
        {
        path:"/",
        component:Home
        },
        {
            path:"/add",
            component:Add
        },
        {
            path:"/update",
            component:Update
        },
        ]
})