<template>
    <div>
        姓名：<input type="text" v-model.trim="user.name"><br>
        年龄：<input type="text" v-model.number="user.age"><br>
        生日：<input type="date" v-model="user.bir"><br>
        <button type="button" @click="save">添加</button>
    </div>
</template>

<script>
    import {add} from "@/api/user"
    export default {
        name: "Add",
        data(){
            return {
                user: {
                    name: "",
                    age: "",
                    bir: ""
                }
            }
        },
        methods: {
            save(){
                const {name,age,bir}=this.user
                add(name,age,bir).then(  resp => {
                    const {status,msg}=resp.data
                    if(status){
                        this.$router.push("/")
                    }else{
                        alert(msg)
                    }
                    /*if(resp.data.status){
                        this.$router.push("/")
                    }else{
                        alert(resp.data.msg)
                    }*/
                })
            }
        }
    }
</script>

