<template>
   <div>
       姓名：<input type="text" v-model.trim="user.name"><br>
       年龄：<input type="text" v-model.number="user.age"><br>
       生日：<input type="date" v-model="user.bir"><br>
       <button type="button" @click="update">更新</button>
   </div>
</template>

<script>
    import {one,edit} from "@/api/user"
    export default {
        name: "Update",
        data(){
            return {
                user: {
                    id: "",
                    name: "",
                    age: "",
                    bir: ""
                }
            }
        },
        created(){
            this.getData()
        },
        methods: {
            getData(){
                one(this.$route.query.id).then( resp => {
                    const {status,data,msg}=resp.data
                    if(status){
                        this.user =data
                    }else{
                        alert(msg)
                    }
                    /*if(resp.data.status){
                        this.user = resp.data.data
                    }else{
                        alert(resp.data.msg)
                    }*/
                } )
            },
            update(){
                const {id,name,bir,age}=this.user
                edit(id,name,age,bir) .then(  resp => {
                    const {status,msg}=resp.data
                    if(status){
                        this.$router.push("/")
                    }else{
                        alert(msg)
                    }
                    /*if(resp.data.status){
                        this.$router.push("/")
                    }else{
                        alert(resp.data.msg)
                    }*/
                })
            }
        }
    }
</script>

